import type { Variants } from 'imports/importNpms'

//?     
//   ,     
const varsPages: Variants = {
	//  
	hidden: {
		y: -20,
		opacity: 0,
	},
	//  
	visible: {
		y: 0,
		opacity: 1,
	},
}
export const confPagesAnim = {
	initial: 'hidden',  //    
	animate: 'visible',
	variants: varsPages, // 
	transition: { duration: 0.7, ease: [0.6, -0.05, 0.01, 0.99] },
}

//   ,     
export const varsText: Variants = {
	//  
	hidden: {
		y: '10%', //     
		opacity: 0,
	},
	//  
	visible: (custom: number) => ({
		y: 0, //     
		opacity: 1,
		transition: {
			delay: custom * 0.2, //  
			duration: 1, //  
			// type: 'tween', //  
			// ease: 'easeInOut', //  
		},
	}),
}
export const confTextAnim = {
	initial: 'hidden', //    
	animate: 'visible', //    
	variants: varsText, //
	exit: { y: '10%', opacity: 0 }, //      
}

export const confTextAnimScroll = {
	initial: 'hidden', //    
	whileInView: 'visible', //    
	variants: varsText, //
	viewport: { once: false, amount: 0.2 },
}

